import React from "react";
import OraSwitcher from "eshop/modules/core/components/ui/OraSwitcher";
import PropositionForDeviceMobileView from "eshop/modules/simfree/components/PropositionForDeviceMobileView"
import OnlineUtils from "eshop/utils/OnlineUtils";
const OfferProductListView = (props) => {

    let proposition=props.offer;
    const dataDescription = props.offer && props.offer.bonusDescriptions && props.offer.bonusDescriptions.find(desc => desc.attribute === "data_desc");
      console.log("DATA DESCRIPTION");
      console.log(props);

    let dataRetention = props.offer.features.getMobileRetentionDesc()

    let dataValue = dataDescription && dataDescription.value || ""
    if(dataRetention) {
        dataValue += dataRetention.value
        dataValue = dataValue.replace(' ','').replace('&nbsp;','')
    }
    const innerHtmlForValue = {__html: dataValue}
    return (
        <OraSwitcher key={props.index+"_switcher"} onChangeHandler={props.onChange} isSelected={props.isSelected} className=" u-padding-all-l  u-padding-top u-padding">
        <PropositionForDeviceMobileView  {...props} key={"desktop_"+proposition.id}/>
            {
                (props.isSelected) &&
                OnlineUtils.updateOgTag('product:retailer_part_no', proposition.id + '^' + props.selectedVariant)
            }
    </OraSwitcher>
)}

export default OfferProductListView;
